<template>
    <div>
        <data-from-plant-form  @recive_Father="reciveData"></data-from-plant-form>
        <div class="data">
            WORDPRESS DATA = {{ $store.state.platform_data }}
        </div>
    </div>
</template>

<script>
import LoginFromStrapi from '../Conning/Login/LoginFromStrapi.vue'
import DataFromPlantForm from '../Conning/PlantForm/DataFromPlantForm.vue'
    export default {
        components: {
            DataFromPlantForm,
                LoginFromStrapi
        },
        name: '',
        data() {
          
            return {
                
            }
        },
        mounted() {
            setTimeout((e) => {
                console.log('go')
                this.$emit('sign_Father', true)
            }, 2000)  
        },
        methods: {
            reciveData(data) {

                try {
                    if (data.params) {

                        this.savingData(data.params)
                        this.$emit('sign_Father', true)
                    }
                } catch(e) { }
            },

            savingData(data) {
                let res = true
                const plant = this.$store.getters.checkPlantfrom()
                if (!plant) {
                    if (this.$store.state.platform_data.userID == data.userID) {
                        res = false
                    }
                }

                if (res) {
                    this.$store.commit('saveFromPlatform', data.params)
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
.data
    font-size: 36px
    color: rgb(0, 168, 243)
</style>


