<template>
    <div>
        <wordpress-app v-if="layout == conf.LAYOUT.WORDPRESS" @sign_Father="plantFinished"></wordpress-app>
        <div v-else></div>

        <div v-if="$store.state.chronus">
            <init-auto v-if="$store.state.user_backend"></init-auto>
        </div>

        <div v-if="!finished">
            <login-from-strapi v-if="this.$store.state.platform_data"></login-from-strapi>
        </div>

        <transition name="fade">
            <init-shimmer v-show="!finished"></init-shimmer>
        </transition>
    </div>
</template>

<script>
import LoginFromStrapi from './components/Conning/Login/LoginFromStrapi.vue'
import InitAuto from './components/Data/InitAuto.vue'
import WordpressApp from './components/PlantForm/WordpressApp.vue'
import InitShimmer from './components/Shimmer/InitShimmer.vue'
    export default {
        components: {
            WordpressApp,
            InitShimmer,
                InitAuto,
                LoginFromStrapi
        },
        name: '',
        data() {
            return {
                layout: '',

                finished: false
            }
        },
        created() {
            // 更改 平台
            this.layout = this.conf.LAYOUT.WORDPRESS

            const _layout = this.$route.query.layout
            if (_layout) {
                this.layout = _layout
            }

            // HTML 模式去除 Shimmer
            if (this.layout == this.conf.LAYOUT.HTML) {
                this.finished = true
            }
        },
        methods: {
            
            plantFinished(v) {
                this.finished = v
            }
        },
    }
</script>

<style lang="sass" scoped>
    
.fade-leave,
.fade-leave-to
    transition: .5s all
    
.fade-leave
    opacity: 1
.fade-leave-to
    opacity: 0

</style>


