<template>
    <div>

    </div>
</template>

<script>
    export default {
        name: '',
        props: {

        },
        data() {
            return {
                unid: null
            }
        },
        mounted() {
            this.unid = this.$store.state.platform_data.userID
            if (this.unid) {
                this.strapiUser(this.unid)
            }
        },

        methods: {

            async strapiUser(_unid) {

                let user = await this.conn.get(
                    this.api.users,
                    '',
                    {
                        'unid': _unid
                    }
                )
                if (user) {
                    if (user.length > 0) {
                        user = user[0]
                        this.$store.commit('saveBackendUser', user)

                        this._login(user.username, user.password)
                    }
                }
            },
            async _login(username, password) {

                try {
                    const res = await this.conn.login({
                        identifier: username,
                        password: password,
                    })

                    if (res) {
                        this.$store.commit('changeAuth', res.data.jwt)
                        // this.$store.commit('saveBackendUser', res.data.user)
                        
                        if (!user.sponser_id) {
                            this.$router.push({ path: '/' })
                        } else {
                            this.$router.push({ path: '/home/user/center' })
                        }
                    }
                } catch(e) {
                    this.view.alert_message('賬號或密碼錯誤！！！')
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


